@import url("https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap");

/* font-family: 'Fira Sans', sans-serif; */

body {
  margin: 0;
  font-family: "Fira Sans", sans-serif;
  /*colors*/
  --color1: rgb(255, 255, 255);
  --color2: yellow;
  --color3: orange;
  --color4: rgb(154, 38, 38);

  --text-focus: rgb(255, 170, 12);
  --text-secondary: white;

  background: var(--color1);
  color: var(--color2);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.BannerContainer {
  animation: shake 5s infinite linear;
}

.BannerContainer img {
  width: 50vw;
}

.CatsContainer {
  display: flex;
  flex-direction: row;
  position: fixed;
  bottom: 0px;
  width: 65vw;
  justify-content: space-between;
}

@keyframes shake {
  0% {
    rotate: 0deg;
  }
  95% {
    rotate: 0deg;
  }
  96% {
    rotate: 4deg;
  }
  97% {
    rotate: -4deg;
  }
  98% {
    rotate: 4deg;
  }
  99% {
    rotate: -4deg;
  }
  100% {
    rotate: 0deg;
  }
}

.CatsContainer img {
  height: 30vh;
  transition: 0.3s;
}

/* .CatsContainer img:hover {} */

.CatT:hover {
  scale: 1.05;
}

.CatD:hover {
  scale: 1.05;
}

.CatL:hover {
  scale: 1.05;
}

/* .CatD {
  filter: grayscale();
} */

/* .CatL {
  filter: grayscale();
} */

@media screen and (max-width: 768px) {
  .BannerContainer img {
    width: 90vw;
  }

  .CatsContainer {
    width: 75vw;
  }
}

@media screen and (max-width: 1130px) {
  .CatsContainer img {
    height: 20vh;
  }
}

@media screen and (max-width: 500px) {
  .CatsContainer img {
    height: 15vh;
  }
}
